html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

:root {
  --active-collor: #00acdc;
}

body {
  margin: 0;
}

header {
  background-color: #fff;
  justify-content: flex-end;
  align-items: center;
  display: flex;
  position: relative;
}

.nav-bar {
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  margin: 0 50px 0 0;
  display: flex;
}

.nav-list {
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

ul :first-child a {
  margin: 0 10px 0 0;
}

ul :last-child a {
  margin: 0 0 0 10px;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin: 0 10px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.logo {
  justify-content: flex-end;
  align-items: center;
  display: flex;
  position: absolute;
  left: 50px;
}

.nav-link:hover, .is-active {
  color: var(--active-collor);
}

.is-active:after {
  color: var(--active-collor);
  content: "";
  background-color: var(--active-collor);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
